import axios from 'axios'


export const onMarkClick = async ({ id, action }) => {
    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
    } 
    
    const body = JSON.stringify({
        [action]: true
    })

    const res = await axios.put(`${process.env.REACT_APP_API_ROOT_URL}/api/emails/email/${id}`, body, config)

    .catch((err) => {
        if (err.response) {
            throw new Error(err.response.data)
        } else {
            throw new Error('There was an error')
        }
    })

    return res.data
}


