import React from 'react'
import { Link } from 'react-router-dom'
import { useQueryClient, useMutation } from 'react-query'
import PropTypes from 'prop-types'
import Loader from 'react-loader-spinner'
import { onMarkClick, onUnmarkClick } from '../api/update'

const Mailbox = ({ data, isFetching, isFetchingNextPage, fetchNextPage, hasNextPage, mailboxCap, mailbox }) => {
    const emptyMailbox = mailbox === 'inbox' ? mailbox  : mailbox + ' mailbox'

    const queryClient = useQueryClient()

    const { mutateAsync: markMutateAsync } = useMutation(onMarkClick, {
        onMutate: async ({ target, id, action }) => {
            queryClient.cancelQueries([mailboxCap, mailbox])
            const previousMails = queryClient.getQueryData([mailboxCap, mailbox])
            queryClient.setQueryData([mailboxCap, mailbox], old => {
                if (action === 'read') {
                    return {
                        ...old,
                        pages: old.pages.map(page => {
                            return {
                                ...page,
                                body: page.body.map(mail => {
                                    if (mail.id === id) {
                                        return { ...mail, read: true }
                                    } else {
                                        return mail
                                    }
                                })
                            }
                        })
                    }
                } else {
                    return {
                        ...old,
                        pages: old.pages.map(page => {
                            return {
                                ...page,
                                body: page.body.filter(mail => mail.id !== id)
                            }
                        })
                    }
                }
            })

            return { previousMails }
        },
        onError: (_err, _, context) => {
            queryClient.setQueryData([mailboxCap, mailbox], context.previousMails)
        },
        onSettled: () => {
            queryClient.invalidateQueries([mailboxCap, mailbox])
        }
    })

    const { mutateAsync: unmarkMutateAsync } = useMutation(onUnmarkClick, {
        onMutate: async ({ target, id, action }) => {
            queryClient.cancelQueries([mailboxCap, mailbox])
            const previousMails = queryClient.getQueryData([mailboxCap, mailbox])
            queryClient.setQueryData([mailboxCap, mailbox], old => {
                if (action === 'read') {
                    return {
                        ...old,
                        pages: old.pages.map(page => {
                            return {
                                ...page,
                                body: page.body.map(mail => {
                                    if (mail.id === id) {
                                        return { ...mail, read: false }
                                    } else {
                                        return mail
                                    }
                                })
                            }
                        })
                    }
                } else {
                    return {
                        ...old,
                        pages: old.pages.map(page => {
                            return {
                                ...page,
                                body: page.body.filter(mail => mail.id !== id)
                            }
                        })
                    }
                }
            })

            return { previousMails }
        },
        onError: (_err, _, context) => {
            queryClient.setQueryData([mailboxCap, mailbox], context.previousMails)
        },
        onSettled: () => {
            queryClient.invalidateQueries([mailboxCap, mailbox])
        }
    })


    const onIconClick = (e, id) => {
        if (e.target.classList.contains('main__mail__content__icons__icon')) {
            e.preventDefault()
            const target = e.target.parentNode.parentNode.parentNode.parentNode

            if (e.target.innerText === 'delete' || e.target.innerText === 'archive' || e.target.innerText === 'mark_email_read') {
                markMutateAsync(({ target, id, action: `${e.target.innerText === 'delete' ? 'trashed' : e.target.innerText === 'archive' ? 'archived' : 'read'}` }))
            } else {
                unmarkMutateAsync(({ target, id, action: `${e.target.innerText === 'restore_from_trash' ? 'trashed' : e.target.innerText === 'unarchive' ? 'archived' : 'read'}` }))
            }
        }
    }

    window.onscroll = () => {
        if ((window.innerHeight + window.scrollY >= document.body.offsetHeight) && hasNextPage) {
            fetchNextPage()
        }
    }


    return (
        <>
            <div className="list-group">
                {data.pages.map((page, i) => {
                    return (
                        <React.Fragment key={i}>
                            { (data.pages.length <= 1 && page.body.length === 0) ? (
                                <div className="main__empty">Your {emptyMailbox} is empty</div>
                            ) : (
                                page.body.map(item => {
                                    return (
                                        <Link className={`main__mail ${item.read ? 'main__read' : ''}`} key={item.id} to={`/${mailbox}/${item.id}`} 
                                            onClick={(e) => onIconClick(e, item.id)}
                                        >
                                            <div className="main__mail__sender-timestamp">
                                                <div className="main__mail__sender-timestamp__sender">{item.sender}</div>
                                                <div className="main__mail__sender-timestamp__timestamp">
                                                    <small>{item.timestamp}</small>
                                                </div>
                                            </div>
    
                                            <div className="main__mail__content">
                                                <div className="main__mail__content__subject-body">
                                                    <span className="main__mail__content__subject-body__subject">{item.subject}</span>
                                                    <span className="main__mail__content__subject-body__dash"> - </span>
                                                    <span className="main__mail__content__subject-body__body">{item.body}</span>
                                                </div>
    
                                                <div>
                                                    <div className="main__mail__content__icons">
                                                        {mailbox === 'archive' || mailbox === 'trash' ? null : (
                                                            <i title="Delete" 
                                                                className="material-icons main__mail__content__icons__icon" 
                                                            >
                                                                delete
                                                            </i>
                                                        )}
    
                                                        {mailbox === 'trash' ? (
                                                            <i title="Restore from trash" 
                                                                className="material-icons main__mail__content__icons__icon"
                                                            >
                                                                restore_from_trash
                                                            </i>
                                                        ) : null}
    
                                                        {mailbox === 'trash' || mailbox === 'sent' || mailbox === 'archive' ? null : (
                                                            <i title="Archive" 
                                                                className="material-icons main__mail__content__icons__icon" 
                                                            >
                                                                archive
                                                            </i>
                                                        )}
    
                                                        {mailbox === 'archive' ? (
                                                            <i title="Unarchive" 
                                                                className="material-icons main__mail__content__icons__icon"
                                                            >
                                                                unarchive
                                                            </i>
                                                        ) : null}
    
                                                        <i title="Mark as read" 
                                                            className={`material-icons main__mail__content__icons__icon 
                                                            ${item.read ? 'hide' : ''}`} 
                                                        >
                                                            mark_email_read
                                                        </i>
    
                                                        <i title="Mark as unread" 
                                                            className={`material-icons main__mail__content__icons__icon 
                                                            ${item.read ? '' : 'hide'}`} 
                                                        >
                                                            mark_email_unread
                                                        </i>
                                                    </div>
                                                </div>
                                            </div>
                                        </Link>
                                    )
                                })
                            )}
                        </React.Fragment>
                    )
                })}
            </div>

            {hasNextPage ? (
                <div className="main__loading" style={{ visibility: `${(isFetching && isFetchingNextPage) ? 'visible' : 'hidden'}` }}>
                    <Loader 
                        type='ThreeDots'
                        color='#fff'
                        height={60}
                        width={60}
                    />
                </div>
            ) : (
                null
            )}

            {(!hasNextPage && data.pages[0].body.length > 0) ? (
                <p className="main__mails-finished">
                    <b>Nothing more to load</b>
                </p>
            ) : null}
        </>
    )
}


Mailbox.propTypes = {
    data: PropTypes.object.isRequired,
    isFetching: PropTypes.bool.isRequired,
    isFetchingNextPage: PropTypes.bool.isRequired,
    fetchNextPage: PropTypes.func.isRequired,
    hasNextPage: PropTypes.bool.isRequired,
    mailbox: PropTypes.string.isRequired,
}

export default Mailbox