import axios from 'axios'
import isTokenValid from '../isTokenValid'

export const login = async ({email, password, user: { user, setUser, setIsAuthenticated }})  => {
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    const body = JSON.stringify({ email, password })

    const res = await axios.post(`${process.env.REACT_APP_API_ROOT_URL}/api/token/`, body, config)

    localStorage.setItem('token', res.data.access)
    localStorage.setItem('email', email)
    setUser({ ...user, email })
    setIsAuthenticated(isTokenValid())
}


export const signup = async ({ name, email, password, confirmation }) => {
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    const body = JSON.stringify({ name, email, password, confirmation })

    await axios.post(`${process.env.REACT_APP_API_ROOT_URL}/api/accounts/signup`, body, config)
}


export const logout = (user, setUser, setIsAuthenticated) => {
    localStorage.removeItem('token')
    localStorage.removeItem('email')

    setUser({ ...user, email: ''})
    setIsAuthenticated(false)
}