import axios from 'axios'


const single = async (key) => {
    const [ , id ] = key.queryKey
    const config = {
        headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
    }

    const res = await axios.get(`${process.env.REACT_APP_API_ROOT_URL}/api/emails/email/${id}`, config)

    .catch((err) => {
        if (err.response) {
            throw new Error(err.response.data)
        } else {
            throw new Error('There was an error')
        }
    })

    res.data.recipients = res.data.recipients.join()
    res.data.recipients = res.data.recipients.replace(',', ', ')

    return res.data
}


export default single