import axios from 'axios'


const mailbox = async key => {
    const [ , mailbox ] = key.queryKey
    let pageParam = key.pageParam

    if (!pageParam) pageParam = 1

    const config = {
        headers: {
            authorization: `Bearer ${localStorage.getItem('token')}`
        }
    }

    const res = await axios.get(`${process.env.REACT_APP_API_ROOT_URL}/api/emails/mailbox/${mailbox}/${pageParam}`, config)

    return res.data
}


export default mailbox