import axios from 'axios'


export const onMarkClick = async ({id, action}) => {
    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
    } 

    const body = JSON.stringify({
        [action]: true
    })

    await axios.put(`${process.env.REACT_APP_API_ROOT_URL}/api/emails/email/${id}`, body, config)
}


export const onUnmarkClick = async ({ id, action }) => {
    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
    } 

    const body = JSON.stringify({
        [action]: false
    })

    await axios.put(`${process.env.REACT_APP_API_ROOT_URL}/api/emails/email/${id}`, body, config)
}