import React, { useState, useContext } from 'react'
import { Link, Redirect } from 'react-router-dom'
import { useMutation } from 'react-query'
import toast from 'react-hot-toast'
import { Helmet } from 'react-helmet'
import { signup, login } from '../api/auth'
import { MailContext } from '../MailContext'
import Auth from '../components/Auth'

const Register = props => {
    const { mutateAsync: registerMutateAsync, isLoading } = useMutation(signup)

    const { mutateAsync: loginMutateAsync } = useMutation(login)

    const [formData, setFormData] = useState({
        name: '',
        email: '',
        password: '',
        confirmation: ''
    })

    const { name, email, password, confirmation } = formData

    const { user: { user, setUser }, auth: { isAuthenticated, setIsAuthenticated } } = useContext(MailContext)

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value })

    const formDetails = [
        {
            isAutoFocus: true,
            type: 'text',
            placeholder: 'Full name',
            name: 'name',
            value: name,
            onChange: onChange
        },
        {
            isAutoFocus: false,
            type: 'email',
            placeholder: 'Email Address',
            name: 'email',
            value: email,
            onChange: onChange
        },
        {
            isAutoFocus: false,
            type: 'password',
            placeholder: 'Password',
            name: 'password',
            value: password,
            onChange: onChange
        },
        {
            isAutoFocus: false,
            type: 'password',
            placeholder: 'Password Again',
            name: 'confirmation',
            value: confirmation,
            onChange: onChange
        }
    ]

    const notifyPasswordMismatchError = () => toast.error((t) => {
        return (
            <span className="alert__span">
                Passwords <b className="alert__span__bold">do not</b> match
                <span className="material-icons alert__span__icon" onClick={() => toast.dismiss(t.id)}>close</span>
            </span>
        )
    })

    const notifyPasswordLengthError = () => toast.error((t) => {
        return (
            <span className="alert__span">
                Password <b className="alert__span__bold">must</b> be at least 8 characters long
                <span className="material-icons alert__span__icon" onClick={() => toast.dismiss(t.id)}>close</span>
            </span>
        )
    })

    const onSubmit = e => {
        e.preventDefault()

        if (password !== confirmation) {
            toast.remove()
            notifyPasswordMismatchError()
        } else if (password.length < 8) {
            toast.remove()
            notifyPasswordLengthError()
        } else {
            const myPromise = registerMutateAsync(({ name, email, password, confirmation}), {
                onSuccess: () => {
                    loginMutateAsync(({ email, password, user: {user, setUser, setIsAuthenticated }}), {
                        onSuccess: () => {
                            props.history.push('/inbox')
                        }
                    })
                }
            })
    
            const notify = () => toast.promise(myPromise, {
                loading: () => <span>Loading <b>PLEASE WAIT!</b></span>,
                success: () => {
                    toast.remove()
    
                    return (
                        <span className="alert__span">
                            You have <b className="alert__span__bold">successfully </b> registered
                            <span className="material-icons alert__span__icon" onClick={() => toast.dismiss()}>close</span>
                        </span>
                    )
                },
                error: () => {
                    toast.remove()
    
                    return (
                        <span className="alert__span">
                            There was an <b className="alert__span__bold">error</b>
                            <span className="material-icons alert__span__icon" onClick={() => toast.dismiss()}>close</span>
                        </span>
                    )
                }
            })
    
            notify()
        }
    }

    if (isAuthenticated) {
        return <Redirect to="/inbox" /> 
    }

    return (
        <div className="container-fluid auth">
            <Helmet>
                <title>Register - Mail</title>

                <meta name="Register to get the best messaging experience" content="registration page" />
            </Helmet>

            <div className="auth__subsection">
                <div className="auth__subsection__subsection">
                    <div className="auth__subsection__subsection__form">
                        <h2 className="auth__subsection__subsection__form__heading">Register</h2>

                        <Auth
                            formDetails={formDetails} 
                            submitValue="Register" 
                            onSubmit={onSubmit} 
                            isLoading={isLoading}
                        />
                    
                        <small className="auth__subsection__subsection__form__small">
                            Already have an account? 
                            <Link className="auth__subsection__subsection__form__small__link" 
                                to="/login"
                            >
                                Login
                            </Link>
                        </small>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Register