import React, { useState, useContext } from 'react'
import { Link, Redirect } from 'react-router-dom'
import { useMutation } from 'react-query'
import toast from 'react-hot-toast'
import { Helmet } from 'react-helmet'
import { login } from '../api/auth'
import { MailContext } from '../MailContext'
import Auth from '../components/Auth'

const Login = props => {
    const [formData, setFormData] = useState({
        email: '',
        password: ''
    })

    const { email, password } = formData

    const { user: { user, setUser }, auth: { isAuthenticated, setIsAuthenticated } } = useContext(MailContext)

    const { mutateAsync, isLoading } = useMutation(login)

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value })

    const formDetails = [
        {
            isAutoFocus: true,
            type: 'email',
            placeholder: 'Email Address',
            name: 'email',
            value: email,
            onChange: onChange
        },
        {
            isAutoFocus: false,
            type: 'password',
            placeholder: 'Password',
            name: 'password',
            value: password,
            onChange: onChange
        }
    ]

    const onSubmit = e => {
        e.preventDefault()

        const myPromise = mutateAsync(({ email, password, user: {user, setUser, setIsAuthenticated }}), {
            onSuccess: () => {
                props.history.push('/inbox')
            }
        })

        const notify = () => toast.promise(myPromise, {
            loading: () => <span>Loading <b>PLEASE WAIT!</b></span>,
            success: () => {
                toast.remove()

                return (
                    <span className="alert__span">
                        You have <b className="alert__span__bold">successfully </b> logged in
                        <span className="material-icons alert__span__icon" onClick={() => toast.dismiss()}>close</span>
                    </span>
                )
            },
            error: () => {
                toast.remove()

                return (
                    <span className="alert__span">
                        There was an <b className="alert__span__bold">error</b>
                        <span className="material-icons alert__span__icon" onClick={() => toast.dismiss()}>close</span>
                    </span>
                )
            }
        })

        notify()
    }

    if (isAuthenticated) {
        return <Redirect to="/inbox" /> 
    }
    
    return (
        <div className="container-fluid auth">
            <Helmet>
                <title>Login - Mail</title>

                <meta name="Login to get the best messaging experience" content="login page" />
            </Helmet>

            <div className="auth__subsection">
                <div className="auth__subsection__subsection">
                    <div className="auth__subsection__subsection__form">
                        <h2 className="auth__subsection__subsection__form__heading">Login</h2>

                        <Auth
                            formDetails={formDetails} 
                            submitValue="Login" 
                            onSubmit={onSubmit} 
                            isLoading={isLoading}
                        />
                    
                        <small className="auth__subsection__subsection__form__small">
                            Don't have an account? 
                            <Link className="auth__subsection__subsection__form__small__link" 
                                to="/register"
                            >
                                Register
                            </Link>
                        </small>
                    </div>
                </div>
            </div>
        </div>
    )
}


export default Login