import React, { useContext } from 'react'
import { Helmet } from 'react-helmet'
import { useInfiniteQuery } from 'react-query'
import { Redirect } from 'react-router-dom'
import Loader from 'react-loader-spinner'
import Mailbox from '../components/Mailbox'
import Layout from '../hocs/Layout'
import { MailContext } from '../MailContext'
import { logout } from '../api/auth'
import mailbox from '../api/mailbox'

const Archive = () => {
    const { data, status, error, isFetching, isFetchingNextPage, fetchNextPage, hasNextPage } = useInfiniteQuery(['Archive', 'archive'], mailbox, {
        getNextPageParam: lastPage => lastPage.next_number ?? false
    })

    const { user: { user, setUser }, auth } = useContext(MailContext)

    if (!auth.isAuthenticated) {
        logout(user, setUser, auth.setIsAuthenticated)
        return <Redirect to="/logout" />
    }

    return (
        <Layout>
            <Helmet>
                <title>Archive - Mail</title>

                <meta name="Access all your archived mails here" content="Archive page" />
            </Helmet>

            <h3 className="main__name">Archive</h3>

            {status === 'loading' ? (
                <div className="main__loading">
                    <Loader
                        type="ThreeDots"
                        color="#fff"
                        height={60}
                        width={60}
                    />
                </div>
            ) : status === 'error' ? (
                <div className="main__empty">There was an error: {error.message}</div>
            ) : (
                <Mailbox 
                    data={data}
                    isFetching={isFetching}
                    isFetchingNextPage={isFetchingNextPage}
                    fetchNextPage={fetchNextPage}
                    hasNextPage={hasNextPage}
                    mailboxCap='Archive'
                    mailbox='archive'
                />
            )}
        </Layout>
    )
}


export default Archive